            ],
            id_location__name:[
                    {required:true,message:'id',trigger:'blur'},

            ],
            id_speaker__name:[
                    {required:true,message:'id',trigger:'blur'},

            ],
            attr:[
                    {required:true,message:'',trigger:'blur'},

            ],
            charge:[
                    {required:true,message:'',trigger:'blur'},

            ],
            pwaccess:[
                    {required:true,message:'',trigger:'blur'},

            ],
            pwcontinuous:[
                    {required:true,message:'',trigger:'blur'},

            ],
            pwdirection:[
                    {required:true,message:'',trigger:'blur'},

            ],
            dooropen:[
                    {required:true,message:'',trigger:'blur'},

            ],
            timebegincheckbegin:[
