    },
     methods:{
        timeup(){
            this.kechengform.timeupdate =(new Date().getTime() - new Date
            ('2000-1-1 00:00:00').getTime())/1000;
        },
        datatrans1(){
                    if(this.kechengform.attr == 0){
                        this.kechengform.attr="";
                    }else if(this.kechengform.attr == 1){
                        this.kechengform.attr="";
                    }else{
                        this.kechengform.attr="";
                    }

                    if(this.kechengform.charge == 0){
