                    }else if(this.kechengform.charge == 1){
                        this.kechengform.charge="";
                    }else{
                        this.kechengform.charge="";
                    }

                    if(this.kechengform.pwaccess == 0){
                        this.kechengform.pwaccess="";
                    }else{
                        this.kechengform.pwaccess="";
                    }

                    if(this.kechengform.pwcontinuous == 0){
