                    }else{
                        this.kechengform.pwcontinuous="";
                    }

                    if(this.kechengform.dooropen == 0){
                        this.kechengform.dooropen="";
                    }else{
                        this.kechengform.dooropen="";
                    }
        },
        datatrans2(){
                    if(this.kechengform.attr == ""){
                        this.kechengform.attr=0;
                    }else if(this.kechengform.attr == ""){
                        this.kechengform.attr=1;
                    }else{
