            this.$nextTick(()=>{
                this.$refs[formName].resetFields();
            });
            //
            this.dialogVisible = false;
            this.isView = false;
            this.isEdit = false;
        },

        //
        viewkecheng(row){
            //
            this.dialogTitle = "";
            //isView
            this.isView = true;
            //
            this.dialogVisible = true;
            //
            this.kechengform = JSON.parse(JSON.stringify(row))
            this.datatrans1();
        },
        //
        updatekecheng(row) {
            //
            this.dialogTitle = "";
            //isEdit
            this.isEdit = true;
            //
            this.dialogVisible = true;
            this.kechengform = JSON.parse(JSON.stringify(row))
            this.datatrans1();
        },
        //
        submitkechengform(formName) {
            this.$refs[formName].validate((valid) =>{
                if (valid) {
                    //
                    if (this.isEdit){
                        this.kechengform.id = Number(this.kechengform.id);
                        this.kechengform.id_location__name = Number(this.kechengform.id_location__name);
                        this.kechengform.id_speaker__name = Number(this.kechengform.id_speaker__name);
                        //this.timeup();
                        this.datatrans2();
                        this.submitupdatakecheng();
                    }else{
                        //
                        this.kechengform.id = Number(this.kechengform.id);
                        this.kechengform.id_location__name = Number(this.kechengform.id_location__name);
                        this.kechengform.id_speaker__name = Number(this.kechengform.id_speaker__name);
                        //this.timeup();
                        this.datatrans2();
                        this.submitaddkecheng()
                    }
                alert('submit!');
              } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        //
        submitaddkecheng(){
            let that = this;
            //Axios
            axios
            .put(that.baseURL + 'CourseInformation/', that.kechengform)
            .then(res=>{
                //
                if(res.data.error_code == 0){//res.data.code == 1
                    // //
                    // that.kecheng = res.data.data;
                    // //
                    // that.total = res.data.data.length;
                    // //
                    // that.getpagekecheng();
                    that.getkecheng();
                    //
                    that.$message({
                        message:'',
                        type:'success'
                    });
                    //
                    that.closeDialogForm('kechengform');
                }else{
                    //
                    that.$message.error(res.data.message);
                }
            })
            .catch(err=>{
                //
                console.log(err);
                that.$message.error("");
            });
        },
        //
        submitupdatakecheng(){
            let that = this;
            //Axios
            axios
            .patch(that.baseURL + 'CourseInformation/', that.kechengform)
            .then(res=>{
                //
                if(res.data.error_code == 0){//res.data.code == 1
                    // //
                    // that.kecheng = res.data.data;
                    // //
                    // that.total = res.data.data.length;
                    // //
                    // that.getpagekecheng();
                    that.getkecheng();
                    //
                    that.$message({
                        message:'',
                        type:'success'
                    });
                    //
                    that.closeDialogForm('kechengform');
                }else{
                    //
                    that.$message.error(res.data.message);
                }
            })
            .catch(err=>{
                //
                that.$message.error("");
            });
        },
