<template>
    <div>
        <el-aside width="200px">
            <!--default-activeindex, :collapse:router-->
            <el-menu  default-active="0" :router="true"  :style="{height:tableheight}">
                <!--indexitem:route-->
                <el-menu-item index="0" :route="{name:''}">
                    <i class="el-icon-s-platform"></i>
                    <span slot="title"> </span>
                </el-menu-item>
                <el-submenu index="1">
                    <template slot="title"><i class="el-icon-user-solid"></i>
                        <span></span>
                    </template>
                        <el-menu-item index="1-1" :route="{name:''}"></el-menu-item>
                        <el-menu-item index="1-2" :route="{name:''}"></el-menu-item>
                </el-submenu>
                <el-menu-item index="2" :route="{name:''}">
                    <i class="el-icon-document"></i>
                    <span slot="title"> </span>
                </el-menu-item>
                <el-menu-item index="3" :route="{name:''}">
                    <i class="el-icon-user"></i>
                    <span slot="title"> </span>
                </el-menu-item>
            </el-menu>
        </el-aside>
    </div>
</template>
<script>
export default {
    props:{
        tableheight:String
    }
 }
</script>

<style lang="css" scoped>
.el-menu{
     height: 100%;
   }
.el-aside .el-menu .el-menu-item,span,i{
    color: #333;
    
   }

</style>


