<template>
    <div>
        <el-form ref="loginForm" :model="loginForm" :rules="loginRules" class="login-form" auto-complete="on" label-position="left">

            <el-form-item prop="user_id" >
                <el-input  v-model="loginForm.user_id" placeholder=" user_id" auto-complete="on"></el-input>
            </el-form-item>

            <el-form-item prop="password" >
                <el-input placeholder=" password" :type="passwordType" v-model="loginForm.password" auto-complete="on"
                @keyup.enter.native="handleLogin">
                    <template slot="suffix">
                        <span class="show-pwd" @click="showPwd" :style="{color:fcolor}">
                        <i class="iconfont icon-originalimage"></i>
                        </span>
                    </template>
                </el-input>
            </el-form-item>
            <el-form-item style="width:100%;">
                <el-button :loading="loading" type="primary" plain style="width:100%;margin-bottom:18px;"   @click.native.prevent="handleLogin"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data(){
        const validatePassword = (rule,value,callback) =>{
            if (value.length < 6)
            callback(new Error('6'))
            else callback()
        }
        return{
            fcolor:'gray',
            passwordType:'password',
            code:'',
            path:'',
            checked:true,
            baseURL:"http://localhost:9000/polls/", //http://10.40.11.195:8000/polls
            loginForm:{
                user_id:'',
                password:'',
            },
            loginRules:{
                user_id:[{required:true,message:'',trigger:'blur'}],
                password:[{required:true,message:'',trigger:'blur'},{validator:validatePassword,trigger:'blur'}],
            },
            loading:false, //
            is_status:false, //
            URLs:'',//
            urlt:''
        }
    },
    mounted(){
        this.creates();
    },
    methods:{
    creates(){
        //this.$bus.$on('login',(val)=>{
        //this.URLs = val[0];
        //console.log(this.URLs);
        //if(this.URLs){
        //    this.urlt = '/administor';
        //}else{
            //this.urlt = '/administor';
        //    }
        //});
    },
    showPwd(){
        if (this.passwordType == 'password') {
        this.passwordType = '';
        this.fcolor='black';

      } else {
        this.passwordType = 'password'
        this.fcolor='gray';
      }
    },

    handleLogin(){  //
        this.$refs.loginForm.validate(valid => {
        if (valid && !this.loading) {
            this.loading = true;

            let that = this;//this

            //AxiosAjax

            axios
            .post(that.baseURL + 'Login/',
            {
                user_id:that.loginForm.user_id,
                password:that.loginForm.password
            }
            )
            .then(function(res){
                //
                if(res.data.code == 1){
                      console.log(res.data.attr);
                      if(res.data.attr == 0 || res.data.attr == 1 || res.data.attr == 2){ 
                           that.$router.push({path: '/administor'});
                       }else if(res.data.attr == 3){
                           that.$router.push({path: '/teacher'});
                       }else{
                           that.$router.push({path: '/students'});
                     }
                    //that.$router.push({path: '/administor'});
                    that.$message({
                        message:'',
                        type:'success'
                    });
                }else {
                    //
                    that.$message.error(res.data.msg);
                    location.reload();
                    that.$message.error("");
                    that.$router.push({path:'/index'});
                    
                }
            })
            .catch(function(err){
                //
                location.reload();
                console.log('');
                that.$message.error("");
                that.$router.push({path:'/index'});
                
            })
        }else{
          return false
        }
      })
    }
    }
}
</script>



