<template>
    <el-container>
        <!--  logo  name="xxxxx()"-->
        <el-header  style="height:60px;">
                <headers Sname=""> 
				    <el-dropdown trigger="hover" slot="right">
					    <span class="el-dropdown-link userinfo-inner"><img src="" /> ,{{nickName}}</span>
					    <el-dropdown-menu slot="dropdown">
						    <el-dropdown-item @click.native="logout()"></el-dropdown-item>
					    </el-dropdown-menu>
				    </el-dropdown>
                </headers>
        </el-header>
        <!---->
        <el-main :style="{height:fullHeight}">
            <el-container >
                <!---->
                   <sidebarst :tableheight="fullHeight" ></sidebarst>
                <!---->
                <el-main :style="{height:fullHeight}">
                    <!---->
					<el-breadcrumb separator-class="el-icon-arrow-right" >
						<el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
							<router-link :to="item.path">{{ item.name }}</router-link>
						</el-breadcrumb-item>
					</el-breadcrumb>
                     <transition name="bounce" mode="out-in">
                        <router-view :tableheight="fullHeight" ></router-view>
                     </transition>
                </el-main>
            </el-container>
        </el-main>
        <!---->
        <el-footer style="height:55px">
            <headers><h5 slot="middle">{{currenttime}}</h5> </headers> 
        </el-footer>
    </el-container>
</template>
<script>
import headers from '../components/headers'
import sidebarst from '../components/sidebarst'
export default {
    data(){
        return{
            nickName: "",
            color1: '#409EFF',
            timer:"",//
            currenttime:'',
            fullHeight:(document.documentElement.clientHeight-115)+"px",//
            is_status:false,
        }
    },
    components:{
        headers,
        sidebarst
    },
    methods:{
        handResize(event){ //
        this.fullHeight=(document.documentElement.clientHeight-115)+"px";
        },
        logout(){
            this.$confirm('','',{
                conformButtonText:'',
                concelButtonText:'',
                type:'warning'
            }).then(()=>{
                sessionStorage.clear(); //sessionstorage
                this.$router.push('/index');
            }).catch(()=>{

            });
        }
    },
    created(){
        window.addEventListener('resize',this.handleResize);

        this.timer=setInterval(()=>{
            this.currenttime=new Date().getFullYear() +"-"+(new Date().getMonth()+1)
            +"-"+new Date().getDate() +" "+new Date().getHours()+":"+new Date().getMinutes()
            +":"+new Date().getSeconds();
            },1000)
    },
    beforeDestory(){
        window.removeEventListener('resize',this.handleResize)
        localStorage.clear;
        clearInterval(this.timer);//
    }
}
</script>
<style scoped>
.el-container {
         padding: 0;
        margin: 0;
        height: 100%;
    }
    .el-main{
        padding: 0;
        margin: 0;
        border-left: 1px solid gray;
    }
    .el-header, .el-footer {
        margin: 0;
        padding: 0;
        background-color:  #b3c0d1
          /*rgb(148, 43, 43); #409EFF;*/
    }
    .el-color-picker{
        line-height: 55px;
    }
    .bounce-enter-active {
     animation: bounce-in .35s;
   }
   .bounce-leave-active {
     animation: bounce-in .35s reverse;
   }
   @keyframes bounce-in {
     0% {
       transform: translateY(15px);
       opacity: 0;
     }
     100% {
       transform: translateY(0px);
       opacity: 1;
     }
   }

.el-breadcrumb{
    padding: 0;
    margin: 0;
    font-size: 15px;
    margin: 15px 5px;
    font-family: "";
}
.userinfo-inner{
	font-family: Arial, Helvetica, sans-serif;
	color: #000;
    font-family: "";
}
.userinfo-inner:hover{
	color: rgb(206, 192, 192);
}
</style>


