<template >
    <div>
        <span  class="left">
                <!-- <img :src="logo |filter" alt=""> -->
                <!-- xxxxlogo -->
                {{Sname}}
            <slot name="left">
                    <!--  -->
            </slot>
        </span>

        <span  class="middle">
            <slot name="middle">
                    <!--  -->
            </slot>
        </span>

        <span  class="right">
             <!--  -->
            {{name}}
            <slot name="right">
                    <!-- -->
            </slot>
        </span>
    </div>

</template>
<script>
import Vue from "vue";
export default {
    //
    props:{
        Sname:String,
        name:String,
        logo:String,
    },

}
</script>
<style scoped>
.left{
        float: left;
        line-height: 50px;
        margin-left: 1%;
        text-align: left;
       font-family: "";
    }
.middle{
        float: left;
        line-height: 50px;
        margin-left: 40%;
        text-align: center;
        font-family: "";
    }
.right{
        float:right;
        line-height:50px;
        /* text-align: right; */
        margin-right: 10%;
       font-family: "";
    }
</style>



