        deleteshebei(row) {
            //
            this.$confirm('id' + row.id + '\t' + row.id_location__name + '',
                '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                //
                let that = this
                //
                axios
                .delete(that.baseURL + 'EquipmentInformation/',{data:{ids:[{"data_id":row.id}]}})
                .then(res => {
                        if (res.data.error_code == 0){
                            // that.shebei = res.data.data;
                            // //
                            // that.total = res.data.data.length;
                            // //
                            // that.getpageshebei();
                            that.getshebei();
                            //shebei
                            //that.shebei = res.data.data;
                            //
                            //that.total = res.data.total_number*that.limits;
                            //
                            that.$message({
                                message: '',
                                type: 'success'
                            });
                        } else {
                            that.$message.error(res.data.message);
                        }
                    })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        //
        deleteshebeis() {
            //
          this.$confirm("" + this.selectshebeis.length + "",
                '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let ids = [];
                for(var i=0;i<this.selectshebeis.length;i++){
                    ids.push({"data_id":this.selectshebeis[i].id});
                }
                //
                let that = this
                //
                axios
                .delete(that.baseURL + 'EquipmentInformation/',{data:{
                ids:ids}
                })
                .then(res => {
                        if (res.data.error_code == 0) {
                            that.getshebei();
                            //
                            that.$message({
                                message: '',
                                type: 'success'
                            });
                        } else {
                            that.$message.error(res.data.message);
                        }
                    })
              }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },

    },
}
</script>
<style>
html,body,.el-container{
    margin: 0px;
    padding:0px;
    height: 100%;
}

.el-dialog .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;

}
.el-dialog .avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.el-dialog  .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.el-dialog .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>



