<template>
<div>
<el-form :inline="true" style="margin-top:20px;">
<el-row style="margin-left:20px;">
    <el-col :span="12">
        <el-form-item label="">
            <el-input v-model="input_string" placeholder="" style="width: 420px;">
            </el-input>
        </el-form-item>
    </el-col>
    <el-col :span="8" style="text-align: right;padding-right:10px;">
        <el-button-group>
            <el-button type="primary" icon="el-icon-search" @click="querykecheng()"></el-button>
            <el-button type="primary" icon="el-icon-tickets" @click="getkecheng()"></el-button>
        </el-button-group>
    </el-col>
</el-row>
</el-form>
    <el-table :data="kecheng" border style="width: 70%;margin-left:20px" size="mini">
        <el-table-column prop="id" label="id" width="120">
        </el-table-column>
        <el-table-column prop="name" label="" width="140">
        </el-table-column>
        <el-table-column prop="timebegin" label="" width="140">
        </el-table-column>
        <el-table-column prop="timeend" label="" width="140">
        </el-table-column>
        <el-table-column prop="id_location__name" label="id" width="140">
        </el-table-column>
        <el-table-column width="140px" fixed="right" label="">
            <template slot-scope="scope">
                <el-button type="success" icon="el-icon-more" size="mini" circle @click="handleclick(scope.row)"></el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-row style="margin-left: 300px;">
        <el-pagination         
	        @size-change="handleSizeChange"
	        @current-change="handleCurrentChange"
	        :current-page="currentpage"
            :page-size="pagesize"
	        layout="prev, pager, next, jumper"
	        :total="total">       
        </el-pagination>
    </el-row>
</div>
</template>
<script>
import axios from 'axios'
export default {
    data(){
        return {
        baseURL:"http://localhost:9000/polls/",
        kecheng:[],//align="center"
        pagekecheng:[],//
        input_string:'', //
        //
        a:'',
        total:0, //
        currentpage:1, //
        pagesize:10,//
        limits:10,
    }
    },
    mounted(){
        //
        this.getkecheng();
    },
     methods:{
        timeup(){
            this.kechengform.timeupdate =(new Date().getTime() - new Date
            ('2000-1-1 00:00:00').getTime())/1000;
        },

        handleclick(row){
         this.$router.push({path:'/kaoqing?index='+row.id})
        },

        getkecheng:function(){
            //thisCourseInformation/
            let that = this
            //AxiosAjax
            axios
            .get(that.baseURL + "CourseInformation/",{params:{page:that.currentpage,limits:that.limits}})
            .then(function(res){
                //
                if(res.data.code == 0){
                    that.$router.push('/index');
                }else{
                    if(res.data.error_code == 0){//res.data.code ===1
                    //
                    that.kecheng = res.data.data;
                    //
                    that.total = res.data.total_number*that.limits;
                    that.$message({
                        message:'',
                        type:'success'
                    });
                    }else {
                        //
                        that.$message.error(res.data.message);
                }
                }
            })
            .catch(function(res){
                //
                console.log(res);
                that.$message.error('');
            })
            },
        //     viewkaoqing(row){
        //     let that = this;
        //     axios
        //     .get(that.baseURL + "StudentAttendance/",{params:{course_plan_id:row.id}})
        //     .then(function(res){
        //         //
        //             if(res.data.error_code == 0){
        //             //
        //             that.kaoqingform = res.data.data;
        //             that.$message({
        //                 message:'',
        //                 type:'success'
        //             });
        //             }else {
        //                 //
        //                 that.$message.error(res.data.message);
        //             }
        //     })
        //     .catch(function(res){
        //         //
        //         console.log(res);
        //         that.$message.error('');
        //     })
        // },
        //
        getpagekecheng(){
            //pageskecheng
            this.pagekecheng = [];
            //
            for(let i=(this.currentpage -1)*this.pagesize; i< this.total;i++){
                //pageshebei
                this.pagekecheng.push(this.kecheng[i]);
                //
                if (this.pagekecheng.length === this.pagesize) break;
            }
        },
        //
        querykecheng(){
            //Ajax--POST-->input_string
            let that = this;
            //Ajax
            axios                                                                                                                 
            .post(
                that.baseURL + "CourseInformation/",
                {
                    input_string: that.input_string
                }
            )
            .then(function(res){
                if(res.data.error_code == 0){
                    //that.getkecheng();
                    //
                    that.kecheng = res.data.data;
                    //
                    that.total = res.data.total_number*that.limits;
                    //
                    that.$message({
                        message:'',
                        type:'success'
                    });
                }else {
                    //
                    that.$message.error(res.data.message);
                }
            })
            .catch(function(err){
                console.log(err);
                that.$message.error("!");
            })
        },

        //
        handleSizeChange(size){
            //
            this.pagesize = size;
            //
            this.getkecheng();
        },

        //
        handleCurrentChange(pageNumber){
            //
            this.currentpage = pageNumber;
            //
            this.getkecheng();
        },

    },
}
</script>
<style>
html,body,.el-container{
    margin: 0px;
    padding:0px;
    height: 100%;
}

.el-dialog .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;

}
.el-dialog .avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.el-dialog  .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.el-dialog .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>



