<template>
<div>
<el-form v-model="userform" :inline="true" style="margin-top:20px;">
<el-row style="margin-left:20px;">
    <el-col :span="12">
        <el-form-item label="">
            <el-input  v-model="input_string" placeholder="" style="width: 420px;">
            </el-input>
        </el-form-item>
    </el-col>
    <el-col :span="8" style="text-align: right;padding-right:10px;">
        <el-button-group>
            <el-button type="primary" icon="el-icon-search" @click="queryuser()"></el-button>
            <el-button type="primary" icon="el-icon-tickets" @click="getuser()"></el-button>
            <el-button type="primary" icon="el-icon-circle-plus-outline" @click="adduser()"></el-button>
        </el-button-group>
    </el-col>
</el-row>
</el-form>
    <el-table :data="user" border style="width: 90%;margin-left:20px" size="mini" @selection-change="handleSelectionChange">
        <el-table-column type="selection">
        </el-table-column>
        <el-table-column type="index" label="" width="60" align="center" fixed="left">
        </el-table-column>
        <el-table-column prop="id" label="id" width="120" align="center">
        </el-table-column>
        <el-table-column prop="nocard" label="" width="140" align="center">
        </el-table-column>
        <el-table-column prop="nouser" label="id" width="140" align="center">
        </el-table-column>
        <el-table-column  width="400px" fixed="right" label="" align="center">
            <template slot-scope="scope">
                <el-button type="success" icon="el-icon-more" size="mini" circle @click="viewuser(scope.row)"></el-button>
                <el-button type="primary" icon="el-icon-edit" size="mini" circle @click="updatauser(scope.row)"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="deleteuser(scope.row)"></el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-row style="margin-top: 10px;">
        <el-col :span="8" style="text-align: left;margin-left:20px">
            <el-button type="primary" icon="el-icon-delete" size="mini" @click="deleteusers()"></el-button>
        </el-col>
        <el-pagination         
	        @size-change="handleSizeChange"
	        @current-change="handleCurrentChange"
	        :current-page="currentpage"
            :page-size="pagesize"
	        layout="prev, pager, next, jumper"
	        :total="total">       
            </el-pagination>
    </el-row>
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="50%"  @close="closeDialogForm('userform')">
        <el-form :model="userform" :rules="rules" ref="userform" :inline=true style="margin-left:20px;" label-width="110px" labei-position="right" size="mini">
            <el-form-item label="ID" prop="id">
                <el-input v-model="userform.id" :disabled="isEdit || isView" suffix-icon="el-icon-edit"></el-input>
            </el-form-item>
            <el-form-item label="" prop="nocard">
                <el-input v-model="userform.nocard" :disabled="isView" suffix-icon="el-icon-edit"></el-input>
            </el-form-item>
            <el-form-item label="id" prop="nouser">
                <el-input v-model="userform.nouser" :disabled="isView" suffix-icon="el-icon-edit"></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
                <el-input v-model="userform.name" :disabled="isView" suffix-icon="el-icon-edit"></el-input>
            </el-form-item>
            <el-form-item label="" prop="psw">
                <el-input v-model="userform.psw" :disabled="isView" suffix-icon="el-icon-edit"></el-input>
            </el-form-item>
            <el-form-item label="" prop="deptid__name">
                <el-input v-model="userform.deptid__name" :disabled="isView" suffix-icon="el-icon-edit"></el-input>
            </el-form-item>
            <el-form-item label="" prop="sex">
                <el-select v-model="userform.sex" :disabled="isView" placeholder="">
                    <el-option labei="" value=""></el-option>
                    <el-option labei="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="attr">
                <el-select v-model="userform.attr" :disabled="isView" placeholder="">
                    <el-option labei="" value=""></el-option>
                    <el-option labei="" value=""></el-option>
                    <el-option labei="" value=""></el-option>
                    <el-option labei="" value=""></el-option>
                    <el-option labei="" value=""></el-option>
                </el-select>
            </el-form-item>
            <!-- <el-form-item label="">
                <el-date-picker type="date" placeholder=""  v-model="userform.timeupdate" value-format="yyyy-MM-dd" :disabled="isView" suffix-icon="el-icon-edit">
                </el-date-picker>
            </el-form-item> -->
            <el-form-item label="" prop="localid">
                <el-input v-model="userform.localid" :disabled="isView" suffix-icon="el-icon-edit"></el-input>
            </el-form-item>
            <el-form-item label="" prop="userex_related_to_user_information__idmanager__name">
                <el-input v-model="userform.userex_related_to_user_information__idmanager__name" :disabled="isView" suffix-icon="el-icon-edit"></el-input>
            </el-form-item>
            <el-form-item label="" prop="userex_related_to_user_information__rem">
                <el-input v-model="userform.userex_related_to_user_information__rem" :disabled="isView" suffix-icon="el-icon-edit"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" size="mini" v-show="!isView" @click="submituserform('userform')"></el-button>
            <el-button type="info" size="mini" @click="closeDialogForm('userform')"></el-button>
        </span>
    </el-dialog>
</div>
</template>
<script>
import axios from 'axios'
export default {
    data(){
        // id
        const rulesID = (rule,value,callback) =>{
            if(this.isEdit){
                callback();
            }
            //axios
            axios
            .post(
                this.baseURL + 'IDEvaluation/',
                {
                    id:value,king:1
                }
            )
            .then((res)=>{
                //
                if (true){
                    if(res.data.exists){
                        callback(new Error("id"));
                    }else{
                        callback();
                    }
                }else{
                    callback(new Error("id"))
                }
            })
            .catch((err)=>{
                //
                console.log(err);
            });

        }

        return {
        baseURL:"http://localhost:9000/polls/",
        user:[],//
        pageuser:[],//
        input_string:'', //
        //
        total:0, //
        currentpage:1, //
        pagesize:10,//
        limits:10,
        dialogVisible:false,
        userform:{
            id:'',
            nocard:'',
            nouser:'',
            name:'',
            psw:'',
            deptid__name:'',
            sex:'',
            attr:'',
            timeupdate:'',
            localid:'',
            userex_related_to_user_information__timeupdate:'',
            userex_related_to_user_information__idmanager__name:'',
            userex_related_to_user_information__rem:'',
        },
        rules:{
            id:[
                    {required:true,message:'id',trigger:'blur'},
                    {validator:rulesID,trigger:'blur'},
            ],
            nocard:[
                    {required:true,message:'',trigger:'blur'},
            ],
            nouser:[
                    {required:true,message:'id',trigger:'blur'},
            ],
            name:[
                    {required:true,message:'',trigger:'blur'},
            ],
            psw:[
                    {required:true,message:'',trigger:'blur'},
            ],
            deptid:[
                    {required:true,message:'',trigger:'blur'},
            ],
            sex:[
                    {required:true,message:'',trigger:'blur'},
            ],
            attr:[
                    {required:true,message:'',trigger:'blur'},
            ],
            timeupdate:[
                    {required:true,message:'',trigger:'blur'},
            ],
            localid:[
                    {required:true,message:'',trigger:'blur'},
            ],
            userex_related_to_user_information__timeupdate:[
                    {required:true,message:'timeupdate',trigger:'blur'},
            ],
            userex_related_to_user_information__rem:[
                    {required:true,message:'',trigger:'blur'},
            ],
            userex_related_to_user_information__idmanager__name:[
                    {required:true,message:'',trigger:'blur'},
            ],
        },

        dialogTitle: "", //

        isView: false, //
        isEdit: false, //

        selectusers: [],//
        selectusersid:[],

        }
    },
    mounted(){
        //
        this.getuser();
    },
     methods:{
         timeup(){
            this.userform.timeupdate =(new Date().getTime() - new Date
            ('2000-1-1 00:00:00').getTime())/1000;
            this.userform.userex_related_to_user_information__timeupdate = (new Date().getTime() - new Date
            ('2000-1-1 00:00:00').getTime())/1000;
        },
        datatrans1(){
             if(this.userform.attr == 0){
                        this.userform.attr = '';
                    }else if(this.userform.attr == 1){
                        this.userform.attr = '';
                    }else if(this.userform.attr == 2){
                        this.userform.attr = '';
                    }else if(this.userform.attr == 3){
                        this.userform.attr = '';
                    }else{
                        this.userform.attr = '';
                    }
                    if(this.userform.sex == 0){
                        this.userform.sex = '';
                    }else{
                        this.userform.sex = '';
                    }
         },
        datatrans2(){
            if(this.userform.attr == ""){
                    this.userform.attr = 0;
            }else if(this.userform.attr == ""){
                    this.userform.attr = 1;
            }else if(this.userform.attr == ""){
                    this.userform.attr = 2;
            }else if(this.userform.attr == ""){
                    this.userform.attr = 3;
            }else{
                    this.userform.attr = 4;
            }
            if(this.userform.sex == ""){
                    this.userform.sex = 0;
            }else{
                    this.userform.sex = 1;
            }
         },
        //
        getuser:function(){
            //this
            let that = this
            //AxiosAjax
            axios
            .get(that.baseURL + 'TeachersInformation/',{params:{page:that.currentpage,limits:that.limits}})
            .then(function(res){
                //
                console.log(res);
                if(res.data.code == 0){
                    that.$router.push('/index');
                }else{
                    if(res.data.error_code == 0){//res.data.code ===1
                    //user
                    that.user = res.data.data;
                    //
                    that.total = res.data.total_number*that.limits;
                    //
                    //that.getpageuser();
                    //
                    that.$message({
                        message:'',
                        type:'success'
                    });
                }else {
                    //
                    that.$message.error(res.data.message);
                }
                }
            })
            .catch(function(err){
                //
                that.$message.error("");
            })
        },

        //
        getpageuser(){
            //pageuser
            this.pageuser = [];
            //
            for(let i=(this.currentpage -1)*this.pagesize; i< this.total;i++){
                //pageshebei
                this.pageuser.push(this.user[i]);
                //
                //if (this.pageuser.length === this.pagesize) break;
            }
        },
        //
        queryuser(){
            //Ajax--POST-->input_string
            let that = this;
            //Ajax
            axios                                                                                                                 //Axios
            .post(
                that.baseURL + "TeachersInformation/",
                {
                    input_string: that.input_string
                }
            )
            .then(function(res){
                if(res.data.error_code == 0){
                    //user
                    //that.user = res.data.data;
                    //
                    //that.total = res.data.data.length;
                    //
                    //that.getpageuser();
                    //that.getuser();
                    //user
                    that.user = res.data.data;
                    //
                    that.total = res.data.total_number*that.limits;
                    //
                    that.$message({
                        message:'',
                        type:'success'
                    });
                }else {
                    //
                    that.$message.error(res.data.msg);
                }
            })
            .catch(function(err){
                console.log(err);
                that.$message.error("!");
            })
        },
        //
        handleSizeChange(size){
            //
            this.pagesize = size;
            //
            this.getuser();
        },
        //
        handleCurrentChange(pageNumber){
            //
            this.currentpage = pageNumber;
            //
            this.getuser();
        },
        //
        handleSelectionChange(data){
            this.selectusers = data;
        },
        //
        adduser(){
            //
            this.dialogTitle = "";
            //
            this.dialogVisible = true;
        },
        //
        closeDialogForm(formName){
            //
            this.userform.id ="";
            this.userform.nocard ="";
            this.userform.nouser ="";
            this.userform.name ="";
            this.userform.psw ="";
            this.userform.deptid ="";
            this.userform.sex  ="";
            this.userform.attr ="";
            this.userform.timeupdate ="";
            this.userform.localid ="";
            this.userform.userex_related_to_user_information__timeupdate ="";
            this.userform.userex_related_to_user_information__idmanager__name ="";
            this.userform.userex_related_to_user_information__rem ="";
            //
            this.$nextTick(()=>{
                this.$refs[formName].resetFields();
            });
            //
            this.dialogVisible = false;
            this.isView = false;
            this.isEdit = false;
        },

        //
        viewuser(row){
            //
            this.dialogTitle = "";
            //isView
            this.isView = true;
            //
            this.dialogVisible = true;
            //
            this.userform = JSON.parse(JSON.stringify(row));
            this.datatrans1();

        },
        //
        updatauser(row) {
            //
            this.dialogTitle = "";
            //isEdit
            this.isEdit = true;
            //
            this.dialogVisible = true;
            //
            this.userform = JSON.parse(JSON.stringify(row));
            this.datatrans1();
        },
        //
        submituserform(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    //
                    if (this.isEdit){
                        this.userform.id = Number(this.userform.id);
                        this.userform.deptid__name = Number(this.userform.deptid__name);
                        this.timeup();
                        //console.log(this.timeupdate);
                        this.datatrans2();
                        this.submitupdatauser();
                    }else{
                        //
                        this.userform.id = Number(this.userform.id);
                        this.userform.deptid__name = Number(this.userform.deptid__name);
                        this.timeup();
                        this.datatrans2();
                        this.submitadduser();
                    }
                alert('submit!');
              } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        //
        submitadduser(){
            let that = this;
            //Axios
            axios
            .put(that.baseURL + 'TeachersInformation/', that.userform)
            .then(res=>{
                //
                if(res.data.error_code == 0){//res.data.code == 1
                    that.$message({
                        message:'',
                        type:'success'
                    });
                    that.getuser();
                    //
                    that.closeDialogForm('userform');
                }else{
                    //
                    that.$message.error(res.data.message);
                }
            })
            .catch(err=>{
                //
                console.log(err);
                that.$message.error("");
            });
        },
        //
        submitupdatauser(){
            let that = this;
            //Axios
            axios
            .patch(that.baseURL + 'TeachersInformation/', that.userform)
            .then(res=>{
                //
                if(res.data.error_code == 0){//res.data.code == 1
                    // //
                    // that.user = res.data.data;
                    // //
                    // that.total = res.data.data.length;
                    // //
                    // that.getpageuser();
                    //
                    that.getuser();
                    that.$message({
                        message:'',
                        type:'success'
                    });
                    //
                    that.closeDialogForm('userform');
                }else{
                    //
                    that.$message.error(res.data.message);
                }
            })
            .catch(err=>{
                //
                //console.log(res);
                that.$message.error("");
            });
        },
        //
        deleteuser(row) {
            //
            this.$confirm('id' + row.id + '\t' + row.nocard + '',
                '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                //
                let that = this
                //
                axios
                .delete(that.baseURL + 'TeachersInformation/',{data:{ids:[{"data_id":row.id}]}})
                .then(res => {
                        if (res.data.error_code == 0) {
                            that.getuser();
                            that.$message({
                                message: '',
                                type: 'success'
                            });
                        } else {
                            that.$message.error('');
                        }
                    })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        //
        deleteusers() {
            //
            this.$confirm("" + this.selectusers.length + "",
                '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let ids = [];
                for(var i=0;i<this.selectusers.length;i++){
                    ids.push({"data_id":this.selectusers[i].id});
                }
                //
                let that = this;
                //
                //console.log(that.selectusers[id])
                axios
                .delete(that.baseURL + 'TeachersInformation/',{data:{
                ids:ids}
                })
                .then(res => {
                        if (res.data.error_code == 0){
                            that.getuser();
                            that.$message({
                                message: '',
                                type: 'success'
                            });
                        } else {
                            that.$message.error('');
                        }
                    })

            }).catch(() => {
                this.$message({
                    type: 'error',
                    message: ''
                });
            });
        },

    },
}
</script>
<style>
html,body,.el-container{
    margin: 0px;
    padding:0px;
    height: 100%;
}

.el-dialog .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;

}
.el-dialog .avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.el-dialog  .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.el-dialog .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>



